import React, {useState, useEffect} from 'react'

const Apitest = () => {
    const [posts, setPosts] = useState([]);

    const obj = { page: "1" };
    
    const method = "POST";
    const body = JSON.stringify(obj);
    const headers = {
        'Accept': 'application/json',
        'Content-Type': 'application/json'
    };

    useEffect(() => {
        fetch('http://localhost:82/api/questions/_list.php', {method, headers, body})
        .then(res => res.json())
        .then(data => {
            setPosts(data);
            console.log(data);
        }).catch((error) => {
            console.log(error);
        });
    },[])

    return (
        <div>
        </div>
    )
}

export default Apitest